@import "../../global.scss";
.epilogue{
    width: 100vw;
    height: 120vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    @include mobile_potrait{
        height: 80vh;
    }
    .epilogueTitle{
        width: 80vw;
        height: 6vh;
        margin-top: 2vh;
        font-size: 2.5vh;
        color: $color1;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile_potrait{
            font-size: 1.5vh;
            height: 5vh;
        }
    }
    .epilogueDesc{
        width: 80vw;
        height: 6h;
        margin-bottom: 2vh;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-direction: column;
        @include mobile_potrait{
            height: 5vh;
        }
        p{
            color: white;
            font-size: 3vh;
            text-align: center;
            width: 100%;
            @include mobile_potrait{
                font-size: 1.5vh;
            }
        }
    }
    .epilogueWrap{
        width: 80vw;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        @include mobile_potrait{
            width: 90vw;
            height: 70vh;
        }
        .epilogueItem{
            width: 30vw;
            height: 20vh;
            margin-left: 2vw;
            margin-right: 2vw;
            margin: 2vh;
            @include mobile_potrait{
                height: 18vh;
                width: 35vw;
            }
            
            .actTitle{
                font-size: 2.5vh;
                color: $color1;
                text-align: center;
                @include mobile_potrait{
                    font-size: 1.5vh;
                }
            }
            .actDesc{
                font-size: 3vh;
                color: white;
                @include mobile_potrait{
                    font-size: 1.5vh;
                }
            }
            &:last-child{
                p{
                    text-align: center;
                }
            }
        }
    }
}