@import "../../global.scss";

.topBar{
    width: 100vw;
    height: 8vh;
    background-color: $color1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    z-index: 5;
    .topContent{
        width: 90vw;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        .topNav{
            display: flex;
            justify-content: flex-end;
            align-items: center;
            width: 100%;
            .topNavMenu{
                width: 80%;
                height: 50%;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                @include mobile_potrait{
                    width: 90vw;
                }
                @include mobile_landscape{
                    width: 80%;
                }
                ul{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    list-style: none;
                    margin:0;
                    width: 40vw;
                    margin-right: 5vh;
                    @include mobile_potrait{
                        width: 100vw;
                        display: none;
                    }
                    @include mobile_landscape{
                        width: 40vw;
                        margin-right: 5vh;
                    }
                    
                    li{
                        display: flex;
                        align-items: center;
                        a{
                            color: $color2;
                            font-size: 2vh;
                            text-decoration: none;
                            font-weight: 600;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            transition: opacity 0.5s ease;
                            &:hover{
                                opacity: 0.9;
                            }
                            @include mobile_potrait{
                                font-size: 1.5vh;
                            }
                            @include mobile_landscape{
                                font-size: 2.5vh;
                            }
                            
                        }
                        
                    }
                    
                }
                .topConnectButton{
                    background-color: $color2;
                    border: none;
                    color: white;
                    width: 15vw;
                    height: 4vh;
                    border-radius: 2.5vh;
                    margin-right: 2vw;
                    font-size: 2vh;
                    cursor: pointer;
                    // position: absolute;
                    @include mobile_potrait{
                        width: 22vw;
                        font-size: 1.5vh;
                        right: 8vh;
                    }
                    @include mobile_landscape{
                        width: 15vw;
                        font-size: 2.5vh;
                    }
                }
                &:hover{
                    opacity: 0.9;
                }
                .hamburger{
                    display: none;
                    @include mobile_potrait{
                        display: flex;
                        width: 4vh;;
                        height: 3vh;
                        right : 20px;
                        top : 20px;
                        flex-direction: column;
                        
                        justify-content: space-around;
                        cursor: pointer;
                        padding-right: 2px;
                        padding-left: 2px;
                        z-index: 4;
                        overflow: hidden;
                    }
                    @include mobile_landscape{
                        display: none;
                    }
                    span{
                        width: 100%;
                        height: 0.7vh;
                        border-radius: 3px;
                        background-color: $color2;
                        transform-origin: left;
                        transition: all 1s ease;
                    }
                    &.active{
                        z-index: 1;
                        span{
                            &:first-child{
                                width: calc(100vh * 0.039);
                                transform: rotate(30deg);
                            }
                            &:nth-child(2){
                                opacity: 0;
                            }
                            &:last-child{
                                width: calc(100vh * 0.039);
                                transform: rotate(-30deg);
                            }
            
                        }
                    }
                }
                
            }
            
            
        }
    }
}