@import "../../global.scss";
.minting{
    width: 100vw;
    height: 52vh;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding-top: 10vh;
    @include mobile_potrait{
        height: 100vh;
        padding-top: 5vh;
    }
    .mintingContent{
        display: flex;
        width: 80%;
        height: 50vh;
        @include mobile_potrait{
            height: 80vh;
            width: 90%;
            align-items: center;
            justify-content: center;
            flex-direction: column;
        }
        .mintingLeft{
            flex: 0.6;
            display: flex;
            align-items: center;
            justify-content: center;
            @include mobile_potrait{
                height: 35vh;
                width: 100%;
            }
            .leftContent{
                width: 90%;
                height: 30vh;
                // background-color: #fff;
                display: flex;
                // flex-direction: column;
                align-items: center;
                justify-content: center;
                color: $color2;
                flex-wrap: wrap;
                @include mobile_potrait{
                    height: 20vh;
                    width: 90%;
                }
                .appStore{
                    width: 42%;
                    height: 10vh;
                    background-color: black;
                    margin-left: 2vh;
                    margin-right: 2vh;
                    border-radius: 2vh;
                    object-fit: 100% 100%;
                    @include mobile_potrait{
                        height: 6vh;
                    }
                    img{
                        width: 100%;
                        height: 100%;
                        border-radius: 2vh;
                    }
                    
                    
                }
                .googlePlay{
                    width: 42%;
                    height: 10vh;
                    background-color: black;
                    margin-left: 2vh;
                    margin-right: 2vh;
                    display: flex;
                    border-radius: 2vh;
                    @include mobile_potrait{
                        height: 6vh;
                    }
                    img{
                        width: 100%;
                        height: 100%;
                        border-radius: 2vh;
                    }
                }
                .windowStore{
                    width: 42%;
                    height: 10vh;
                    background-color: black;
                    display: flex;
                    border-radius: 2vh;
                    @include mobile_potrait{
                        height: 6vh;
                    }
                    img{
                        width: 100%;
                        height: 100%;
                        border-radius: 2vh;
                    }
                }
            }
        }
        .mintingRight{
            flex: 0.4;
            display: flex;
            align-items: center;
            justify-content: center;
            .rightContent{
                height: 45vh;
                width: 45vh;
                background-color: $color1;
                color : $color2;
                object-fit: cover;
                border-radius: 10vh;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;
                @include mobile_potrait{
                    height: 35vh;
                    width: 35vh;
                }
                .aboutMintingTitle{
                    width: 90%;
                    text-align: center;
                    margin-top: 5vh;
                    font-size: 2.5vh;
                    @include mobile_potrait{
                        font-size: 1.5vh;
                    }
                }
                .aboutMintingDesc{
                    width: 80%;
                    font-size: 2vh;
                    text-align: center;
                    @include mobile_potrait{
                        font-size: 1.5vh;
                    }
                }
                .learnMoreButton{
                    width: 10vw;
                    height: 4vh;
                    margin-bottom: 3vh;
                    background-color: $color2;
                    color: $color1;
                    border: none;
                    cursor: pointer;
                    border-radius: 1vh;
                    font-size: 2vh;
                    @include mobile_potrait{
                        font-size: 1.5vh;
                        width: 30vw;
                    }
                }
            }
        }
    }
}