@import "../../global.scss";
.noktypes{
    width: 100vw;
    height: 110vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    @include mobile_potrait{
        height: 65vh;
    }
    .noktypesTitle{
        width: 80vw;
        height: 6vh;
        margin-top: 2vh;
        font-size: 2.5vh;
        color: $color1;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile_potrait{
            font-size: 1.5vh;
            height: 5vh;
        }
    }
    .noktypesContent{
        width: 80vw;
        height: 98vh;
        
        margin-bottom: 2vh;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        @include mobile_potrait{
            height: 55vh;
            width: 90vw;
        }
        .typesItem{
            width: 22vw;
            height: 31vh;
            margin: 1vh;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: column;
            cursor: pointer;
            @include mobile_potrait{
                height: 17vh;
                width: 25vw;
            }
            .itemImg{
                height: 30vh;
                width: 30vh;
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                .itemImgBg{
                    position: absolute;
                    top: 0;
                    height: 100%;
                    border-radius: 2vh;
                }
                .itemImgBluer{
                    position: absolute;
                    height: 120%;
                }
                .itemNokImg{
                    position: absolute;
                    height: 90%;
                }
                transition: all 0.3s ease;
                &:hover{
                    transform: scale(1.02);
                }
            }
            .typeName{
                font-size: 3vh;
                color: $color1;
                margin: 1vh;
                @include mobile_potrait{
                    font-size: 1.5vh;
                }

            }
        }
        
    }
}