@import "../../global.scss";
.home{
    width: 100vw;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    @include mobile_potrait{
        height: 92vh;
    }
    @include mobile_landscape{
        height: 160vh;
    }
    .nokHeadImgs{
        width: 100vw;
        height: 50vh;
        background-color: $color1;
        position: relative;
        align-items: center;
        justify-content: center;
        display: flex;
        top: 0;
        
        @include mobile_potrait{
            height: 40vh;
        }
        .headImg{
            width: 12vw;
            margin-top: 10vh;
            margin-right: 1vw;
            margin-left: 1vw;
            z-index: 2;
            cursor: pointer;
            transition: all 0.3s ease;
            display: flex;
            &:hover{
                transform: scale(1.02);
            }
            @include mobile_potrait{
                margin-top: 5vh;
            }
            @include mobile_landscape{
                width: 15vw;
            }
        }
    }
    .homeContainer{
        width: 100vw;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: relative;
        top: 0;
        @include mobile_potrait{
            top: -10vh;
        }
        
        .homeImg{
            position: absolute;
            top: 0;
            width: 100vw;
            height: 100%;
            object-fit: cover;
        }
        .homeContainerTitle{
            width: 70%;
            height: 100vh;
            position: absolute;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: space-between;
            @include mobile_potrait{
                top:0;
                height: 60vh;
            }
            @include mobile_landscape{
                height: 100vh;
            }
            .head{
                margin-top: 10vh;
                font-size: 18vh;
                font-weight: 500;
                @include mobile_potrait{
                    font-size: 5vh;
                    margin-top: 12vh;
                }
                @include mobile_landscape{
                    margin-top: 5vh;
                }
            }
            .artTitle{
                font-size: 10vh;
                font-weight: 500;
                color: white;
                margin-bottom: 4vh;
                @include mobile_potrait{
                    font-size: 3.5vh;
                }
            }
        }
        
    }
}