@import "../../global.scss";

.history{
    width: 100vw;
    height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    @include mobile_potrait{
        height: 50vh;
    }
    .historyTitle{
        width: 80%;
        height: 6vh;
        margin-top: 2vh;
        font-size: 2.5vh;
        color: $color1;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile_potrait{
            font-size: 1.5vh;
            height: 5vh;
            margin-top: 1vh;
           
        }
    }
    .historyDesc{
        width: 80%;
        height: 48vh;
        margin-bottom: 2vh;
        display:-webkit-box;
        display:-webkit-flex;
        display:-webkit-flexbox;
        display:-moz-flex;
        display:-moz-box;
        display:-ms-flexbox;
        display:flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-direction: column;
        @include mobile_potrait{
            width: 90%;
        }
        p{
            color: white;
            font-size: 2.5vh;
            text-align: center;
            width: 100%;
            @include mobile_potrait{
                font-size: 1.5vh;
            }
        }
    }
}