@import "../../global.scss";

.footer{
    width: 100vw;
    height: 30vh;
    display: flex;
    justify-content: center;

    @include mobile_potrait{
        height: 20vh;
    }
    
    .footNav{
        width: 80vw;
        height: 20vh;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: white;
        @include mobile_potrait{
            width: 90vw;
            height: 20vh;
        }
        >*{
            height: 100%;
            width: 20vw;
            transition: all 0.5s ease;
            @include mobile_potrait{
                width: 20vw;
                height: 5vh;
            }
            .title{
                font-size: 3vh;
                font-weight: 800;
                cursor: pointer;
                &:hover{
                    opacity: 0.8;
                }
                @include mobile_potrait{
                    font-size: 1.3vh;
                }
            }
            .menu{
                font-size: 2vh;
                margin-top: 2vh;
                cursor: pointer;
                // display: flex;
                p{
                    margin-top: 2vh;
                    &:hover{
                        opacity: 0.8;
                    }
                    @include mobile_potrait{
                        font-size: 1vh;
                        margin-top: 1vh;
                    }
                }
            }
        }
        .socialLinks{
            .menu{
                display: flex;
                align-items: center;
                justify-content: flex-start;
                .socialIcon{
                    width: 4vh;
                    height: 4vh;
                    margin-right: 1.5vh;
                    margin-left: 1.5vh;
                    background-color: #fff;
                    border-radius: 2vh;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    @include mobile_potrait{
                        width: 2.5vh;
                        height: 2.5vh;
                        margin-right: 1vh;
                        margin-left: 0;
                    }
                    &:hover{
                        opacity: 0.8;
                    }
                    a{
                        text-decoration: none;
                        font-size: 2.5vh;
                        color: $color2;
                        @include mobile_potrait{
                            font-size: 1.5vh;
                        }
                    }
                }
            }
        }
    }
}