@import "../../global.scss";
.utility{
    height: 60vh;
    width: 100vw;
    display:-webkit-box;
    display:-webkit-flex;
    display:-webkit-flexbox;
    display:-moz-flex;
    display:-moz-box;
    display:-ms-flexbox;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    @include mobile_potrait{
        height: 40vh;
    }
    .utilityTitle{
        width: 80%;
        height: 6vh;
        margin-top: 2vh;
        font-size: 2.5vh;
        color: $color1;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile_potrait{
            font-size: 1.5vh;
            height: 5vh;
            margin-top: 1vh;
           
        }

    }
    .utilityContent{
        width: 80%;
        height: 48vh;
        margin-bottom: 2vh;
        display:-webkit-box;
        display:-webkit-flex;
        display:-webkit-flexbox;
        display:-moz-flex;
        display:-moz-box;
        display:-ms-flexbox;
        display:flex;
        @include mobile_potrait{
            font-size: 1.5vh;
            width: 90%;
            height: 30vh;
        }
        .utilityLeft{
            flex: 0.6;
            display: flex;
            align-items: center;
            justify-content: center;
            .leftContent{
                width: 100%;
                height: 80%;
                font-size: 3vh;
                display: flex;
                justify-content: space-between;
                line-height: 4vh;
                color: white;
                min-width: 54vw;
                @include mobile_potrait{
                    font-size: 1.3vh;
                    line-height: 2vh;
                }
                p{
                    width: 100%;
                }
            }

        }
        .utilityRight{
            flex: 0.4;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            min-width: 36vw;
            .rightContent{
                // width: 70%;
                height: 80%;
                display: flex;
                align-items: center;
                justify-content: center;
                
                img{
                    height: 100%;
                    @include mobile_potrait{
                        height: 60%;
                        
                    }
                }
            }
        }
    }
}