@import "global.scss";
.app{
    width: 100vw;
    background-color: rgb(78, 43,1);
    position:relative;
}
.sections{
    width: 100vw;
    position: relative;
    top: 8vh;
    
    scrollbar-width: none;//for firefox
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    >*{
        width: 100vw;
    }
    .sectionContent{
        background-image: linear-gradient(rgb(78, 43,1), #1F1100);
    }
}