@import "../../global.scss";
.faq{
    height: 60vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    @include mobile_potrait{
        height: 35vh;
    }
    .faqTitle{
        width: 80%;
        height: 6vh;
        margin-top: 2vh;
        font-size: 2.5vh;
        color: $color1;
        display: flex;
        align-items: center;
        justify-content: center;
        @include mobile_potrait{
            height: 5vh;
            font-size: 1.5vh;
            margin-top: 1vh;
        }
    }
    .faqContent{
        width: 80%;
        height: 48vh;
        margin-bottom: 2vh;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        @include mobile_potrait{
            height: 30vh;
            margin-bottom: 1vh;
        }
        .faqLine{
            width: 100%;
            // background-color: rgb(78, 43,1);
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            .faqQuetion{
                // width: 60%;
                color: white;
                font-size: 3vh;
                margin-top: 2vh;
                // background-color: rgb(179, 88, 88);
                cursor: pointer;
                transition: all 0.3s ease;
                text-align: center;
                &:hover{
                    transform: scale(1.02);
                }
                @include mobile_potrait{
                    font-size: 1.5vh;
                }
            }
            .faqAnswer{
                text-align: center;
                width: 60%;
                height: inherit;
                display: none;
                color: $color1;
                font-size: 2vh;
                margin-top: 1vh;
                line-height: 1.5;
                @include mobile_potrait{
                    width: 90%;
                    font-size: 1.2vh;
                }
                &.active{
                    display:block;
                }
            }
        }
        
    }
}