$height : 50vh;
$color1: rgb(246, 193, 127);
$color2: rgb(78, 43,1);
$width : 768px;

@mixin mobile_potrait{
    @media screen and (max-width: $width) and (orientation:portrait){
        @content
    }
};
@mixin mobile_landscape{
    @media screen and (max-height: $width) and (orientation:landscape){
        @content
    }
}