@import "../../global.scss";
.nokkers{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    @include mobile_potrait{
        height: 70vh;
    }
    .nokkersContent{
        width: 80%;
        height: 90%;
        display: flex;
        @include mobile_potrait{
            width: 90%;
        }
        .nokkersLeft{
            flex: 0.5;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: column;
            .nokkersPost{
                width: 80%;
                height: 25vh;
                margin:3vh;
                @include mobile_potrait{
                    width: 90%;
                }
                .postTitle{
                    font-size: 2vh;
                    color: $color1;
                    margin-top: 2vh;
                    @include mobile_potrait{
                        font-size: 1.5vh;
                    }
                }
                .postDesc{
                    font-size: 3vh;
                    margin-top: 2vh;
                    color: white;
                    @include mobile_potrait{
                        font-size: 1.5vh;
                    }
                }
            }
        }
        .nokkersRight{
            flex: 0.5;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: column;
            @include mobile_potrait{
                width: 90%;
            }
            .nokkersPost{
                width: 80%;
                height: 25vh;
                margin:3vh;
                .postTitle{
                    font-size: 2vh;
                    color: $color1;
                    margin-top: 2vh;
                    @include mobile_potrait{
                        font-size: 1.5vh;
                    }
                }
                .postDesc{
                    font-size: 3vh;
                    margin-top: 2vh;
                    color: white;
                    @include mobile_potrait{
                        font-size: 1.5vh;
                    }
                }
            }
        }
    }

}